* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; /*como se va a dibujar esos bordes de las cajas q creamos alrededor de los elementos*/
}
html,
body {
  background-color: #1b1b32;
}

.App {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: sans-serif;
}

.titulo-nombre {
  color: aliceblue;
  padding: 0.5rem;
}

.contenedor-principal {
  height: 550px;
  min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
